.container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.app {
  text-align: center;
  padding: 50px;
  border-radius: 8px;
  background-color: #fff;
  min-width: 600px;
}
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 16px;
  color: #000;
  background-color: #002102;
}

#root {
  min-height: 100vh;
}

p {
  margin: 0 0 8px;
}

button {
  border-radius: 5px;
  padding: 8px 16px;
  margin-left: 8px;
}

